.main-nav {
  &__wrapper {
    @include vp-1023 {
      z-index: 100;
      position: fixed;
      top: 79px;
      left: 0;
      height: calc(100vh - 79px);
      width: 100%;
      background-color: $color-atomic;
      clip-path: inset(0 0 100% 0);
      transition: clip-path $trans-600;
      box-sizing: border-box;
    }
  }

  &__list {
    display: flex;
    margin: 0;
    margin-right: 20px;
    margin-left: 60px;
    padding: 0;

    list-style: none;

    @include vp-1023 {
      flex-direction: column;
      font-size: 24px;
      margin-left: 20px;
    }
  }

  &__item {
    margin-right: 20px;

    @include vp-1023 {
      margin-bottom: 10px;
    }
  }

  &__link {
    color: $color-default-white;
    cursor: pointer;

    &.is-active {
      opacity: 0.6;
    }
  }

  &__toggle {
    display: none;
    padding: 0;
    background: none;
    border: none;

    @include vp-1023 {
      z-index: 99;
      position: absolute;
      display: block;
      margin: 0;
      right: 20px;
      top: 26px;
      width: 30px;
      height: 30px;
    }
  }
}

.is-open .main-nav__wrapper {
  clip-path: inset(0 0);
}

.is-open .main-nav__toggle {
  right: 37px;
}
