.intro {
  position: relative;
  display: flex;
  align-items: center;
  min-height: 700px;

  @include vp-1023 {
    min-height: 500px;
  }

  @include vp-767 {
    min-height: 350px;
  }
}

.intro img {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.intro::after {
  content: '';
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba($color-default-white, 0.7);
}

.intro__grid {
  display: flex;
  justify-content: center;
}

.intro__container {
  max-width: 80%;

  @include vp-767 {
    max-width: initial;
  }
}

.intro__title {
  @include title-h1;
  margin-top: 0;
  margin-bottom: 40px;
  text-align: center;
  color: $color-solid-pink;

  @include vp-767 {
    margin-bottom: 20px;
  }
}

.intro__text p {
  @include title-h2;
  text-transform: lowercase;
  text-align: center;
  margin: 0;
}

