.services__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.services__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 30px;
  margin-bottom: 50px;
  width: 100%;
  padding: 0;
  list-style: none;

  @include vp-767 {
    grid-template-columns: 1fr;
    row-gap: 30px;
  }
}

.services__item {
  position: relative;
  @include aspect-ratio(320, 240);
}

.services__text {
  @include primary-text;
  width: 90%;

  p {
    margin: 0;
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }


}
