

.about__text {
  @include primary-text;
  margin-bottom: 60px;
}

.about__text p {
  margin-top: 0;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }
}

.about__list {
  margin: 0;
  padding: 0;
  margin-left: 80px;
  list-style: none;

  @include vp-767 {
    margin-left: 0;
  }
}

.about__item {
  display: grid;
  grid-template-columns: min-content 1fr;
  column-gap: 50px;
  margin-bottom: 30px;

  &:last-child {
    margin-bottom: 0;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
  }
}

.about__item-icon {
  position: relative;
  grid-row: 1/3;
  display: flex;
  width: 84px;
  height: 84px;
  border-radius: 50%;
  background-color: $color-atomic;

  @include vp-767 {
    justify-self: center;
    margin-bottom: 5px;
  }

  &::before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    border: 1px solid $color-gull-grey;
    border-radius: 50%;
    animation: 2s infinite ease-in-out pulse;

    @keyframes pulse {
      0% {
        opacity: 1;
        transform: scale(1, 1);
      }

      50% {
        opacity: 0.5;
      }

      100% {
        opacity: 0;
        transform: scale(1.3,1.3);
      }
    }
  }

  svg {
    margin: auto;
    color: $color-default-white;
    width: 60%;
    height: 60%;
  }
}

.about__item-title {
  @include title-h3;
  margin: 0;
  margin-bottom: 10px;
  padding-top: 10px;
  color: $color-solid-pink;

  @include vp-767 {
    text-align: center;
    margin-bottom: 0;
  }
}

.about__item-text {
  @include primary-text;

  @include vp-767 {
    text-align: center;
  }
}
