.title {
  text-align: center;
  margin-top: 0;

  &--h2 {
    @include title-h2;
    padding-bottom: 20px;
    margin-bottom: 40px;
  }

  &--underline {
    position: relative;
    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      display: block;
      width: 80px;
      height: 3px;
      background-color: $color-gull-grey;
    }
  }
}
