.service-card {
  position: relative;
  transition: transform $trans-default;
  box-shadow: 0 0px 20px 2px rgba($color-default-black, 0.3);
  border-radius: 10px;


  &::after {
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    pointer-events: none;
    background-color: rgba($color-default-black, 0.3);
    border-radius: 10px;
  }

  &:hover {
    transform: perspective(1px) scale(1.02, 1.02);
    .service-card__text {
      color: $color-solid-pink;
    }
  }
}

.service-card a {
  display: block;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.service-card img {
  z-index: -1;
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}

.service-card__text {
  @include primary-text;
  z-index: 1;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 80%;
  margin: 0;
  padding-top: 10px;
  padding-bottom: 10px;
  transform: translate(-50%, -50%);
  background-color: $color-default-white;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  box-shadow: 0 0px 20px 2px rgba($color-default-black, 0.3);
  transition: color $trans-default;
  pointer-events: none;

  @include vp-767 {
    width: 100%;
  }
}
