.equipment__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.equipment__item {
  display: grid;
  grid-template-columns: 4fr 2fr;
  column-gap: 40px;
  margin-bottom: 30px;

  &:last-child {
    margin-bottom: 0;
  }

  @include vp-767 {
    grid-template-columns: 1fr;

  }
}

.equipment__inner-wrap {
  padding-top: 20px;
}

.equipment__item-text {
  @include primary-text;

  p {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.equipment__item-title {
  @include title-h3;
}

.equipment__item-img {
  img {
    width: 100%;
    height: auto;
  }
}
